html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --scissors-grad-from: hsl(39, 89%, 49%);
  --scissors-grad-to: hsl(40, 84%, 53%);
  --paper-grad-from: hsl(230, 89%, 62%);
  --paper-grad-to: hsl(230, 89%, 65%);
  --rock-grad-from: hsl(349, 71%, 52%);
  --rock-grad-to: hsl(349, 70%, 56%);
  --lizard-grad-to: hsl(261, 73%, 60%);
  --lizard-grad-from: hsl(261, 72%, 63%);
  --cyan-from: hsl(189, 59%, 53%);
  --cyan-to: hsl(189, 58%, 57%);
  --dark-text: hsl(229, 25%, 31%);
  --score-text: hsl(229, 64%, 46%);
  --header-outline: hsl(217, 16%, 45%);
  --radial-grad-from: hsl(214, 47%, 23%);
  --radial-grad-to: hsl(237, 49%, 15%);
}

:root {
  --font-body: "Barlow Semi Condensed", sans-serif;
}

.container {
  max-width: 67.5rem;
  margin: 0 auto;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  font-weight: 700;
}

p {
  margin: 0;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}